@charset "utf-8";

.column-global {
  display: flex;
  background: #f5f7f9;
  min-width: 1920px;
}
.column-global .column-navi {
  flex: 0 0 240px;
}
.column-global .column-content {
  flex: 1 1 auto;
  padding: 20px;
  background: #fff;
  min-height: 100vh;
}

header {
  height: 100px;
}
nav {
  padding: 20px;
}
nav h1 {
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}
nav li {
  margin-bottom: 5px;
}
nav li a {
  display: block;
  height: 51px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding-left: 60px;
}
nav li a.active {
  background: #fff;
  font-weight: bold;
}








