@import './reset.css';
@import './layout.css';
@import './components.css';

#page-dashboard section {
  background: #f5f7f9;
  border-radius: 5px;
  padding: 20px;
  margin-bottom: 20px;
  min-height: 270px;
}
#page-dashboard .row {
  margin-right: -10px;
  margin-left: -10px;
  display: flex;
}
#page-dashboard .col {
  padding-right: 10px;
  padding-left: 10px;
  flex: 1;
}
#page-dashboard .row .col:first-child {
  flex-basis: 930px;
}
#page-dashboard h2 {
  margin-bottom: 20px;
}


